/* Styling the container for the hero section */
.heropage {
  position: absolute;
  color: teal;
  margin-top: 100px;
  margin-left: 100px;
}

/* Styling the inner container for the hero section */
.inside-heropage {
  line-height: 1.5;
  padding-top: 20px;
}

/* Styling the heading within the inner container */
.inside-heropage h1 {
  font-size: 40px;
}

/* Styling the paragraph within the inner container */
.inside-heropage p {
  font-size: 17px;
}

/* Styling the span element within the inner container */
span {
  color: teal;
  font-weight: bold;
}

/* Styling the line within the inner container */
.line1 {
  height: 2px;
  background-color: teal;
  width: 12%;
  margin-left: 0px;
  margin-top: 0px;
  position: absolute;
}

/* Styling the button container within the inner container */
.btn1 {
    margin-top:30px; 
}

/* Styling the link within the button container */
.btn1 a {
    background-color:teal; 
    color:white; 
    padding:10px 25px; 
    text-decoration:none; 
    border-radius:30px; 
}
.btn1 a:hover {
    background:black; 
    color:teal; 
}

/* Styling the icon within the link */
.btn1 i {
    color:black; 
    padding:0px 10px; 
}
.btn1 i:hover {
    color:white; 
}

/* Styling the poster container */
.poster {
    margin-top:130px; 
    margin-right:10px; 
}

/* Styling the image within the poster container */
.poster img {
    height:320px; 
    border-radius:100px; 
    float:right; 
    width:900px; 
}
