/* Styling the container for the menu */
.menu-container {
  position: fixed;
  top: 0;
  right: 0;
  width: 40%;
  max-width: 500px;
  height: 100%;
  background-color: rgb(145, 143, 143);
 /* transform: translateX(100%);
  transition: transform 0.3s ease-in-out;*/
}

/* Styling the open state for the menu container */
/*.menu-container.open {
  transform: translateX(0);
}*/

/* Styling the close button for the menu */
.menu-close-button {
  position: absolute;
  top: 10px;
  left: 10px;
}

