/* Styling the container for the menu contents */
.container {
    display:block;
    /*flex-wrap:wrap;*/
    justify-content:space-between;
    /*align-items:flex-start; /* Align items to the top */
    padding-top:20px; 
}

/* Styling the heading within the container */
.container h2 {
    font-size:20px; 
    margin-bottom:10px; 
    color: teal;
}

/* Styling the paragraph within the container */
.container p {
    font-size:16px; 
    margin-bottom:5px; 
}

/* Styling the links within the container */
.container a {
    text-decoration:none; 
    color:black; 
}

