/* Styling the unordered list that contains the search results */
.search-results {
  list-style: none;
  margin: 0;
  padding: 0;
}

/* Styling the list items that represent individual search results */
.search-results li {
  background-color: #eee;
  margin-bottom: 10px;
  padding: 10px;
}

/* Styling the heading within each search result */
.search-results h2 {
  margin: 0;
}

/* Styling the image within each search result */
.search-results img {
  max-width: 100%;
}

/* Styling the search input */
.search-input {
  width: 50%;
  margin-left: 250px;
  margin-top: 50px;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
}
